<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col cols="12">
      <base-material-card
        color="primary"
        inline
        icon="mdi-mail"
        title="Mail"
        class="px-5 py-3"
      >
        <v-form
          ref="form"
          lazy-validation
        >
          <v-container class="py-0">
            <crud ref="crud" />

            <v-btn
              color="primary"
              @click="submit"
            >
              Create
            </v-btn>
          </v-container>
        </v-form>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>
  import Crud from './Crud'

  export default {
    name: 'Create',
    components: { Crud },
    methods: {
      submit () {
        if (!this.$refs.form.validate()) {
          return
        }

        this.$http.post('/portal/organization', JSON.stringify(this.$refs.crud.api))
          .then((response) => {
            this.$toasted.global.success(response.data.message)
            this.$router.push({ name: 'organization' })
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
    },
  }
</script>

